import * as React from 'react';
import { useState, useEffect, useContext } from 'react'; 
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import axios from 'axios';
import { Link, Navigate, useNavigate } from 'react-router-dom';
import context from '../context';
import { request, postUserLogin } from '../api/api';

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright  '}
      <a color="inherit" href="https://mui.com/">
        Your Website
      </a>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const theme = createTheme();

export default function SignIn() {
    const { setToken } = useContext(context);
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    let navigate = useNavigate();

    const login = (data) => {
        if (data.get('email') !== "" && data.get('password') !== "") {
            postUserLogin({
                username: data.get('email'),
                password: data.get('password'),
            })
            .then((res) => {
                console.log(res);
                if (res.data.message) {
                    axios.defaults.headers.common["authorization"] = res.data.token; // axios  token
                    setToken(res.data.token); //  context
                    localStorage.setItem("token", res.data.token); // 
                    alert("!");
                    if (data.get('email') === "managertest" && data.get('password') === "1222444446666666") {
                        navigate('/manager');
                        window.open("https://thomas0913.github.io/nft_manager_interface_system/");
                    }
                    else {
                        navigate(`/member/${data.get('email')}`);
                    }
                }
                if (res.data.error === "ACCOUNT_NOT_EXIST") {
                    alert("");
                }
                if (res.data.error === "WRONG_PASSWORD") {
                    alert("");
                }
            })
            .catch((err) => {
                console.log(err);
            });
        }
        else if (data.get('email') === "") {
            alert("!");
        }
        else {
            alert("!");
        }
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        console.log({
            email: data.get('email'),
            password: data.get('password'),
        });
        setUsername(data.get('email'));
        setPassword(data.get('password'));
        login(data);
    };

    return (
        <ThemeProvider theme={theme}>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign in
                    </Typography>
                    <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="Email Address"
                            name="email"
                            autoComplete="email"
                            autoFocus
                        />
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label="Password"
                            type="password"
                            id="password"
                            autoComplete="current-password"
                        />
                        <FormControlLabel
                            control={<Checkbox value="remember" color="primary" />}
                            label="Remember me"
                        />
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                        >
                        Sign In
                        </Button>
                        <Grid container>
                            <Grid item xs>
                                <Link to='/user/forgot_password' variant="body2">
                                    Forgot password?
                                </Link>
                            </Grid>
                            <Grid item>
                                <Link to='/user/signup' variant="body2">
                                    {"Don't have an account? Sign Up"}
                                </Link>
                            </Grid>
                        </Grid>
                    </Box>
                </Box>
                <Copyright sx={{ mt: 8, mb: 4 }} />
            </Container>
        </ThemeProvider>
    );
}