import React from "react";
//  bootstrap
import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Nav,NavItem,
  Row,Col,
  Button,
  Container,
} from 'reactstrap'; // reactstrap 
import { Link, useNavigate,Navigate, useParams } from 'react-router-dom';
import { request, getUserStatusOfNft } from '../api/api';

function MemberPage() {
    const navigate = useNavigate();
    const { username } = useParams();

    const checkMemberNftStatus = () => {
        getUserStatusOfNft({
            username: username,
        })
        .then((res) => {
            console.log(res);
            if (res.data.message === "HAVE_NFT_AND_HAVE_WALLRT_ADDRESS") {
                navigate(`/member/${username}/nftExistAndHaveWalletAddress`);
            }
            if (res.data.message === "HAVE_NFT_BUT_WALLET_ADDRESS_NOT_FOUND") {
                navigate(`/member/${username}/nftExistButWalletAddressNotFound`);
            }
            if (res.data.error) {
                navigate(`/member/${username}/NftNotFound`);
            }
        })
        .catch((err) => {
            console.log(err);
        });
    }

    return (
        <div>
            <Container>
                <Row>
                    <p>So happy you jsut login successfully</p>
                    <p>Only the Member can access if had login.</p>
                </Row>
                <Button onClick={checkMemberNftStatus}>my NFT</Button>
                <p>Wellom back { username }</p>
                <Button onClick={() => navigate('/')}>LogOut</Button>
            </Container>
        </div>
    );
}

export default MemberPage;