import { useState, useEffect, useContext } from "react";
import { BrowserRouter, HashRouter, Route, Routes, Navigate, useNavigate } from 'react-router-dom';
//  bootstrap
import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Nav,NavItem,
  Row,Col,
  NavLink
} from 'reactstrap'; // reactstrap 
import axios from 'axios';
import Context from "./context";
import Home from './components/Home';
import ForgotPassword from './components/ForgotPassword';
import MemberPage from './components/MemberPage';
import SignIn from './components/SignIn';
import SignUp from './components/SignUp';
import { request, getUserList } from './api/api';
import NftExistAndHaveWalletAddress from './components/nftCollection/NftExistAndHaveWalletAddress';
import HaveNftButWalletAddressNotFound from './components/nftCollection/HaveNftButWalletAddressNotFound';
import NftNotFound from './components/nftCollection/NftNotFound';
import ManagerPage from "./components/ManagerPage";

const PrivateRoute = ({ component, auth }) => {
    const { token } = useContext(Context);
    if (auth || token) {
        return component;
    } else {
        return <Redirect to="/user/login" />;
    }
};
const Redirect = ({ to }) => {
    let navigate = useNavigate();
    useEffect(() => {
        navigate(to, true);
    }, []);
    return null;
};

function App() {
  const [token, setToken] = useState(localStorage.getItem("token"));
  const [auth, setAuth] = useState();

  useEffect(() => {
    const  token = localStorage.getItem("token");
    if (token) {
      axios.defaults.headers.common["authorization"] = token;
      setToken(token);
    }
    else {
      setToken(null);
    }
  }, []);

  /*
  useEffect(() => {
      getUserList()
        .then(() => setAuth(true))
        .catch(() => setAuth(false));
  }, [token]);
  */

  return (
    <div className="App">
      <Context.Provider
        value={{
          token,
          setToken,
        }}
      >
        <HashRouter>
          <Routes>
            <Route path='/' element={<PrivateRoute component={<Home />} auth={auth} />}/>
            <Route path='/member/:username' element={<MemberPage />}/>
            <Route path='/user' />
            <Route path='/user/forgot_password' element={<ForgotPassword />}/>
            <Route path='/user/login' element={<SignIn />} />
            <Route path='/user/signup' element={<SignUp />} />
            <Route path='/member/:username/nftExistAndHaveWalletAddress' element={<NftExistAndHaveWalletAddress />}/>
            <Route path='/member/:username/nftExistButWalletAddressNotFound' element={<HaveNftButWalletAddressNotFound />}/>
            <Route path='/member/:username/NftNotFound' element={<NftNotFound />}/>
            <Route path='/manager' element={<ManagerPage />} />
            <Route path='/redirect' element={<Navigate to='/'/>}/>
          </Routes>
        </HashRouter>
      </Context.Provider>
    </div>
  );
}

export default App;
